"use client";
import React, { useState, useEffect } from "react";
import {
  BrowserRouter as Router,
  Route,
  Routes,
  Link,
  Navigate,
} from "react-router-dom";
import { useWeb3 } from "@/contexts/web3Context";
import { Button } from "@/components/ui/button";
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Badge } from "@/components/ui/badge";
import {
  History,
  User,
  Wheat,
  Box,
  Truck,
  Store,
  ShoppingBag,
  LogOut,
} from "lucide-react";
import UserRegistration from "./registration/page";
import TransactionHistory from "./transaction-history/page";
import FarmerDashboard from "./dashboards/FarmerDashboard";
import CollectorDashboard from "./dashboards/CollectorDashboard";
import TransporterDashboard from "./dashboards/TransporterDashboard";
import DistributorDashboard from "./dashboards/DistributorDashboard";
import RetailerDashboard from "./dashboards/RetailerDashboard";

interface HomeProps {}

const DashboardLayout = ({ children, userRole, onDisconnect }) => {
  return (
    <div className="min-h-screen bg-gray-50">
      <nav className="bg-white shadow-sm">
        <div className="container mx-auto px-4 py-3">
          <div className="flex justify-between items-center">
            <div className="flex items-center space-x-4">
              <Wheat className="w-6 h-6" />
              <h1 className="text-xl font-bold">AgriSupplyChain</h1>
            </div>
            <div className="flex items-center gap-4">
              <Badge className="bg-green-100 text-green-800">
                Role: {userRole}
              </Badge>
              <Button
                variant="outline"
                className="flex items-center gap-2"
                onClick={onDisconnect}
              >
                <LogOut className="w-4 h-4" />
                Disconnect
              </Button>
            </div>
          </div>
        </div>
      </nav>
      <main className="container mx-auto px-4 py-8">{children}</main>
    </div>
  );
};

const Home: React.FC<HomeProps> = () => {
  const { connect, disconnect, account, isActive, contract, error } = useWeb3();
  const [connectionStatus, setConnectionStatus] = useState<string>("");
  const [userRole, setUserRole] = useState<string>("");
  const [showRegistration, setShowRegistration] = useState<boolean>(false);
  const [registrationComplete, setRegistrationComplete] =
    useState<boolean>(false);

  const handleConnection = async () => {
    try {
      setConnectionStatus("Connecting...");
      if (window.ethereum) {
        await window.ethereum.request({
          method: "eth_requestAccounts",
        });
        await connect();
        // After successful connection, immediately show registration
        setShowRegistration(true);
      } else {
        setConnectionStatus("Please install MetaMask!");
      }
    } catch (err: any) {
      console.error(err);
      setConnectionStatus(err.message || "Failed to connect");
    }
  };

  // Effect to check user role whenever registration might have completed
  useEffect(() => {
    const checkUserRole = async () => {
      if (isActive && account && contract) {
        try {
          const role = await contract.methods.userRoles(account).call();
          if (role && role !== "") {
            setUserRole(role);
            setShowRegistration(false);
            setRegistrationComplete(true);
          }
        } catch (err) {
          console.error("Error checking role:", err);
        }
      }
    };

    checkUserRole();
  }, [isActive, account, contract, registrationComplete]);

  // Check for MetaMask on component mount
  useEffect(() => {
    if (!window.ethereum) {
      setConnectionStatus(
        "MetaMask is not installed. Please install MetaMask to use this application."
      );
    }
  }, []);

  if (!isActive) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <Card className="w-full max-w-md">
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Wheat className="w-6 h-6" />
              Welcome to AgriSupplyChain
            </CardTitle>
          </CardHeader>
          <CardContent>
            {connectionStatus && (
              <Alert className="mb-4">
                <AlertDescription>{connectionStatus}</AlertDescription>
              </Alert>
            )}
            {error && (
              <Alert className="mb-4" variant="destructive">
                <AlertDescription>{error}</AlertDescription>
              </Alert>
            )}
            <Button onClick={handleConnection} className="w-full">
              Connect with MetaMask
            </Button>
          </CardContent>
        </Card>
      </div>
    );
  }

  if (showRegistration) {
    return (
      <div className="container mx-auto p-4">
        <UserRegistration contract={contract} account={account} />
      </div>
    );
  }

  return (
    <Router>
      <div className="min-h-screen bg-gray-50">
        <nav className="bg-white shadow-sm">
          <div className="container mx-auto px-4 py-3">
            <div className="flex justify-between items-center">
              <div className="flex items-center space-x-4">
                <Wheat className="w-6 h-6" />
                <h1 className="text-xl font-bold">AgriSupplyChain</h1>
              </div>
              <div className="flex items-center gap-4">
                <Badge className="bg-green-100 text-green-800">
                  Role: {userRole}
                </Badge>
                <Button
                  variant="outline"
                  className="flex items-center gap-2"
                  onClick={disconnect}
                >
                  <LogOut className="w-4 h-4" />
                  Disconnect
                </Button>
              </div>
            </div>
          </div>
        </nav>
        <main className="container mx-auto px-4 py-8">
          <h2>Welcome, {userRole}!</h2>
          <div className="mt-6">
            <Tabs defaultValue="dashboard" className="space-y-6">
              <TabsList className="grid w-full grid-cols-2">
                <TabsTrigger value="dashboard">
                  <Link to="/dashboard">Dashboard</Link>
                </TabsTrigger>
                <TabsTrigger value="history">
                  <Link to="/history">Transaction History</Link>
                </TabsTrigger>
              </TabsList>

              <Routes>
                <Route
                  path="/dashboard"
                  element={
                    <DashboardLayout
                      userRole={userRole}
                      onDisconnect={disconnect}
                    >
                      {FarmerDashboard()}
                    </DashboardLayout>
                  }
                />
                <Route
                  path="/history"
                  element={
                    <DashboardLayout
                      userRole={userRole}
                      onDisconnect={disconnect}
                    >
                      <TransactionHistory
                        contract={contract}
                        account={account}
                      />
                    </DashboardLayout>
                  }
                />
                <Route path="*" element={<Navigate to="/dashboard" />} />
              </Routes>
            </Tabs>
          </div>
        </main>
      </div>
    </Router>
  );
};

export default Home;
